import React from 'react'
import PropTypes from 'prop-types'

const Section = ({ activeStep, currStep, item, getValueProps }) => (
  <div style={{ display: (activeStep === currStep) || (!activeStep && currStep === 0) ? '' : 'none' }}>
    <div key={`sectionlabel-${currStep}`}>
      {item.fields.map(({ component: FieldComponent, name, ...props }, idx) => (
        <FieldComponent name={name} key={`field-${currStep}-${idx}`}
          {...props}
          {...getValueProps(name)}
        />
      ))}
    </div>
  </div>
)

Section.propTypes = {
  activeStep: PropTypes.number,
  currStep: PropTypes.number,
  item: PropTypes.object,
  getValueProps: PropTypes.func
}

export default Section
