import React from 'react'
import PropTypes from 'prop-types'
import { Button, Icon } from 'semantic-ui-react'

const style = {
  zIndex: 10,
  top: '1rem',
  right: '1rem',
  position: 'absolute'
}

const StyleButtons = ({ currStyle, mapStyles, setCurrStyle }) => (
  <Button.Group style={style}>
    {
      mapStyles.map((theStyle, idx) => (
        <Button icon key={`stylebutton${idx}`}
          // color={theStyle.color || null}
          active={idx === currStyle}
          onClick={() => setCurrStyle(idx)}>
          <Icon name={theStyle.icon} />
        </Button>
      ))
    }

  </Button.Group>
)

StyleButtons.propTypes = {
  currStyle: PropTypes.number,
  mapStyles: PropTypes.array,
  setCurrStyle: PropTypes.func
}

export default StyleButtons
