import React from 'react'
import PropTypes from 'prop-types'
import { Button } from 'semantic-ui-react'
import { withCreateStructure } from '../../../graphql/queries'
import { withReduxHome } from '../../../redux/withRedux'
import { compose } from 'recompose'

const AddButton = ({ createStructure, editStructure }) => (
  <Button
    className='addButton'
    icon='plus'
    circular
    color='green'
    onClick={(e) => {
      e.preventDefault()
      createStructure({ body: '{}' }).then(res => {
        try {
          editStructure(res.data.createBDAPOC.id)
        }
        catch (e) { }
      })
      //
    }}
  />
)

AddButton.propTypes = {
  createStructure: PropTypes.func,
  editStructure: PropTypes.func
}

export default compose(
  withCreateStructure,
  withReduxHome
)(AddButton)
