import React from 'react'
import PropTypes from 'prop-types'
import { Segment, Input, Header, Item } from 'semantic-ui-react'
import Note from './Note'
import { compose, branch, renderComponent } from 'recompose'

const Photos = ({ label, notePrompt, note, value, hasNote, setNote }) => (
  <Segment basic className='BDAFormField'>
    <Header as='h3'>{label}</Header>
    <Input type='file' accept='image/*' fluid multiple color='red' />
    {!hasNote ? null
      : <Note
        label={notePrompt}
        value={note}
        setValue={setNote}
        modalTitle={`${label}: ${notePrompt}`}
      />}
  </Segment>
)

const Present = ({ label, value }) => (
  <Item>
    <Item.Content>
      <Item.Header as='h4'>{label}</Item.Header>
      <Item.Description>
        {value || <h4 style={{ color: 'red' }}>Not Entered</h4>}
      </Item.Description>
    </Item.Content>
  </Item>
)
Present.propTypes = Photos.propTypes

Photos.propTypes = {
  label: PropTypes.string,
  multiple: PropTypes.bool,
  placeholder: PropTypes.object, // This is for design only
  options: PropTypes.array,
  value: PropTypes.any,
  setValue: PropTypes.func,

  // Now the note stuff
  note: PropTypes.string,
  notePrompt: PropTypes.string,
  hasNote: PropTypes.bool,
  setNote: PropTypes.func
}

export default compose(
  branch(({ present }) => !!present, renderComponent(Present))
)(Photos)
