import { compose, withHandlers } from 'recompose'
import { withGetStructure, withUpdateStructure } from './queries'
import { debounce } from 'lodash'

// Utilizing closures to debounce functions from props.
// If debounce is applied within the component, the "debounced function"
// will change each time. By defining it statically here, react will always
// consider this to be the same function.
const debounceCall = debounce(func => func(), 1000)

export default compose(
  // Here's the query that gives us structure
  withGetStructure,
  withUpdateStructure,
  // ONCE we mount the control we should initialize it
  withHandlers({
    getValueProps: ({ structure, updateStructure, ...props }) => (name) => {
      let newValProps = {
        value: null,
        note: null
      }
      const newProps = {
        setValue: newVal => {
          console.log('setValue called', { structure, name, newVal, updateStructure })
          let oldVal = { }
          try {
            oldVal = structure.body[name]
          }
          catch (e) { }

          const newObj = { [name]: Object.assign({}, oldVal, { value: newVal }) }
          if (structure && structure.id) {
            // Debouncing is critical so that we don't get a network call for every keystroke
            debounceCall(async () => {
              console.log('ACTUAL CALL')
              await updateStructure({
                id: structure.id,
                body: Object.assign({}, structure.body, newObj)
              })
            })
          }
        },
        setNote: newNote => {
          console.log('setNote called', { structure, name, newNote, updateStructure })
          let oldValues = { }
          try {
            oldValues = structure.body[name]
          }
          catch (e) { }

          const newObj = { [name]: Object.assign({}, oldValues, { note: newNote }) }
          if (structure && structure.id) {
            // Debouncing is critical so that we don't get a network call for every keystroke
            debounceCall(async () => {
              console.log('ACTUAL CALL')
              await updateStructure({
                id: structure.id,
                body: Object.assign({}, structure.body, newObj)
              })
            })
          }
        }
      }
      try {
        newValProps = structure.body[name]
      }
      catch (e) { }

      return Object.assign({}, newValProps, newProps)
    }
  })
)
