import { isNumeric } from './util'
// // Just set some dummy macbounds for now.
/**
 *
 * @param {*} acc  the accumulator we're comparing to
 * @param {*} pt  [Easting, Nothing]
 */
export function findBounds (pts) {
  try {
    return pts.filter(pt => !!pt).reduce((bds, pt) => {
      if (!pt) return bds
      const newAcc = !bds ? [null, null, null, null] : bds.slice()

      if (!newAcc[0] || newAcc[0] > pt[0]) newAcc[0] = pt[0]
      if (!newAcc[1] || newAcc[1] > pt[1]) newAcc[1] = pt[1]
      if (!newAcc[2] || newAcc[2] < pt[0]) newAcc[2] = pt[0]
      if (!newAcc[3] || newAcc[3] < pt[1]) newAcc[3] = pt[1]
      return newAcc
    }, null)
  }
  catch (e) {
    console.error('findBounds', e)
  }
}

/**
 * Find the average of a bunch of points
 * @param {*} pts
 */
export function findCenter (pts) {
  try {
    const sums = pts
      .filter(pt => !!pt)
      .reduce((acc, pt) => [acc[0] + pt[0], acc[1] + pt[1]], [0, 0])
    return [sums[0] / pts.length, sums[1] / pts.length]
  }
  catch (e) {}
}

/**
 * Given a range of data points. find the y-extent and buffer it by a percent
 * @param {*} allDataPts
 * @param {*} lastDrawLocation
 * @param {*} buffer
 **/
export function findYdomain (allDataPts, lastDrawLocation, buffer = 0.05) {
  // console.log('findYdomain RUN')
  try {
    const filteredys = allDataPts
      // Either don't filter (if we aren't zoomed) or filter to the left and the right
      .filter(p => !lastDrawLocation || (p.x >= lastDrawLocation.left && p.x <= lastDrawLocation.right))

    if (filteredys.length === 0) return

    const rawEdges = [
      Math.min(...filteredys.map(dp => (dp.y - (dp.err ? dp.err : 0)))),
      Math.max(...filteredys.map(dp => (dp.y + (dp.err ? dp.err : 0))))
    ]

    const range = rawEdges[1] - rawEdges[0]
    // Buffer by %
    const newRange = [rawEdges[0] !== 0 ? rawEdges[0] - range * buffer : 0, rawEdges[1] + range * buffer]

    return newRange
  }
  catch (e) {
    console.error(e)
    return null
  }
}

/**
 * return the greatest character count of the y vals
 * @param {*} allDAtaPts
 */
export function maxYChars (allDAtaPts) {
  try {
    const maxYChars = Math.max(...allDAtaPts.map(p => {
      if (isNumeric(p.y)) return p.y.toFixed(1).length
      else return p.y.toString().length
    }))
    return maxYChars
  }
  catch (e) {
    return 5
  }
}
