import React from 'react'
import PropTypes from 'prop-types'
import BaseMap from '../../common/maps/BaseMap'
import { Icon, Popup } from 'semantic-ui-react'
import { AutoSizer } from 'react-virtualized'
import { Marker } from 'react-map-gl'
import { findBounds, findCenter } from '../../../lib/mapUtil'
import { compose, withProps } from 'recompose'
import { mapBoxToken } from '../../../config'

const MarkerMap = ({ data, center, bounds, ...props }) => (
  <div style={{ minHeight: '100%' }}>
    <AutoSizer>
      {({ height, width }) => (
        <BaseMap
          {...props}
          mapboxApiAccessToken={mapBoxToken}
          maxBounds={bounds}
          longitude={center[0]}
          latitude={center[1]}
          size={{ height: height < 500 ? 500 : height, width }} >
          {
            // Render props here
            (viewport) => data.map(({ latitude, longitude }, idx) => (
              <Marker viewport={viewport} key={`marker-${idx}`}
                longitude={longitude}
                latitude={latitude}
                offsetLeft={-20}
                offsetTop={-30} >
                <Popup
                  flowing
                  trigger={
                    <Icon
                      className='markerIcon'
                      name='map marker'
                      size='large'
                      color='red'
                      style={{ textShadow: '2px 2px 2px #00000099' }}
                    />
                  }>
                  {/* Hello: ({latitude}, {longitude}) */}
                </Popup>
              </Marker>
            ))
          }
        </BaseMap>
      )}
    </AutoSizer>
  </div>
)

MarkerMap.propTypes = {
  data: PropTypes.arrayOf(PropTypes.shape({
    latitude: PropTypes.number,
    longitude: PropTypes.number
  }))
}

export default compose(
  withProps(({ structures }) => {
    const noData = {
      latitude: 49.3050394,
      longitude: -123.1398254
    }
    const data = []
    structures.forEach(struct => {
      try {
        const { latitude, longitude } = JSON.parse(struct.body)['location'].value
        data.push({ latitude, longitude })
      }
      catch (e) {

      }
    })
    return {
      data,
      center: data.length > 0 ? findCenter(data.map(d => ([d.longitude, d.latitude]))) : [noData.longitude, noData.latitude],
      bounds: data.length > 0 ? findBounds(data.map(d => ([d.longitude, d.latitude]))) : [noData.longitude - 0.01, noData.latitude - 0.01, noData.longitude + 0.01, noData.latitude + 0.01]
    }
  })
)(MarkerMap)
