import React from 'react'
import ReactMapGL from 'react-map-gl'
import PropTypes from 'prop-types'
import { numericArrayCompare } from '../../../lib/util'
import WebMercatorViewport from 'viewport-mercator-project'
import MapStyles from './styles'
import { compose, withStateHandlers, lifecycle, defaultProps } from 'recompose'
import StyleButtons from './styles/StyleButtons'
import './BaseMap.css'

/**
 * Note that this component technically has state but it is designed to be functionally stateless
 */
const BaseMap = ({
  children, size: { height, width }, viewport, mapStyles, currStyle,
  mapboxApiAccessToken, setCurrStyle, onViewportChange, onMapDataEvent,
  stylechooser
}) => (
  <ReactMapGL
    {...viewport}
    height={height}
    width={width}
    mapStyle={mapStyles[currStyle].style}
    onData={onMapDataEvent}
    onViewportChange={onViewportChange}
    mapboxApiAccessToken={mapboxApiAccessToken} >
    { stylechooser ? <StyleButtons currStyle={currStyle} mapStyles={mapStyles} setCurrStyle={setCurrStyle} /> : null }
    {/* Augment children with some props they need to survive in this harsh world */}
    {
      children && Array.isArray(children)
        ? children.map(c => handleChild(c, viewport, height, width))
        : handleChild(children, viewport, height, width)
    }
  </ReactMapGL>
)

function handleChild (child, viewport, height, width) {
  return typeof child === 'function'
    ? child(viewport, height, width)
    : child
}

BaseMap.propTypes = {
  // Anything else we want to add to the map
  children: PropTypes.any,
  deckelements: PropTypes.array,
  data: PropTypes.array,

  // Some switches to make things easier
  stylechooser: PropTypes.bool,
  lockPan: PropTypes.bool,
  lockZoom: PropTypes.bool,
  lockToBounds: PropTypes.bool,
  maxZoom: PropTypes.number,
  minZoom: PropTypes.number,

  minPitch: PropTypes.number,
  maxPitch: PropTypes.number,
  pitch: PropTypes.number,

  maxBounds: PropTypes.array,
  InnerBounds: PropTypes.array,

  mapStyles: PropTypes.array,

  // Just some default props we probably won't set.
  latitude: PropTypes.number,
  longitude: PropTypes.number,
  zoom: PropTypes.number,

  viewport: PropTypes.object.isRequired,
  currStyle: PropTypes.number.isRequired,

  onViewportChange: PropTypes.func.isRequired,
  onMapDataEvent: PropTypes.func,
  setCurrStyle: PropTypes.func.isRequired,

  // Passed in from elsewhere
  mapboxApiAccessToken: PropTypes.string.isRequired,

  // We get these props from calculating the window size at a higher level
  size: PropTypes.shape({
    width: PropTypes.number.isRequired,
    height: PropTypes.number.isRequired
  })
}

export default compose(
  defaultProps({
    stylechooser: true,
    zoom: 12,
    latitude: 49.3050394,
    longitude: -123.1398254,
    size: {
      height: 500,
      width: 600
    },
    mapStyles: MapStyles,
    lockToBounds: true
  }),
  withStateHandlers(
    ({ maxBounds, latitude, longitude, zoom, pitch, minPitch, maxPitch, bearing }) => ({
      currStyle: 0,
      viewport: {
        latitude: maxBounds ? (maxBounds[1] + maxBounds[3]) / 2 : latitude,
        longitude: maxBounds ? (maxBounds[0] + maxBounds[2]) / 2 : longitude,
        zoom: zoom,
        minPitch: minPitch,
        maxPitch: maxPitch,
        bearing: bearing || 0,
        pitch: pitch || 0
      }
    }), {
    // This one's a thunk!!
      setCurrStyle: () => (currStyle) => ({ currStyle }),
      recalcZoomBounds: ({ viewport }, { maxBounds, size, longitude, latitude }) => (nextProps, force = false) => {
        // Can't do anything without any bounds
        if (!maxBounds && !nextProps.maxBounds) return

        // Any time the bounds change we want to recalc the props
        let minlng, minlat, maxlng, maxlat
        const { maxZoom, minZoom } = nextProps
        if (nextProps.maxBounds) [minlng, minlat, maxlng, maxlat] = nextProps.maxBounds
        else [minlng, minlat, maxlng, maxlat] = maxBounds

        // Bounds change
        const boundsChange = nextProps.maxBounds && !numericArrayCompare(nextProps.maxBounds, maxBounds)
        let sizeChange = null
        if (nextProps.size) {
          sizeChange = nextProps.size.width !== 0 && (nextProps.size.width !== size.width || nextProps.size.height !== size.height)
        }

        if (boundsChange) [minlng, minlat, maxlng, maxlat] = nextProps.maxBounds

        let newLat = viewport.latitude
        let newLong = viewport.longitude
        let zoom = viewport.zoom
        let newPitch = viewport.pitch
        let newBearing = viewport.Bearing

        if (boundsChange || sizeChange || force) {
          // On the first run we're going to try to center the bounds.
          const { width, height } = nextProps.size
          try {
            const wmview = new WebMercatorViewport({ width: width > 0 ? width : size.width, height: height > 0 ? height : size.height })
            console.log('before boundschange', { wmview, bds: [ [minlng, minlat], [maxlng, maxlat] ] })
            const fittedBounds = wmview.fitBounds(
              [ [minlng, minlat], [maxlng, maxlat] ],
              { padding: 20, offset: [0, -40] }
            )
            newLat = fittedBounds.latitude
            newLong = fittedBounds.longitude
            zoom = fittedBounds.zoom
          }
          catch (e) {
            // Sometimes we don't have a valid bound. Don't make a big deal out of it.
            console.error(e)
          }

          const newViewport = Object.assign({}, viewport, { latitide: newLat, longitude: newLong, zoom, bearing: newBearing, pitch: newPitch })
          const zoombounds = {
            max: maxZoom || viewport.zoom + 6,
            min: minZoom || viewport.zoom - 6
          }

          return { viewport: newViewport, zoombounds }
        }
      },
      onViewportChange: ({ zoombounds, viewport: oldViewport }, { maxBounds, lockPan, lockToBounds, lockZoom, longitude, latitude }) => (viewport) => {
        // Here we set/reset the bounds
        if (lockPan) {
          viewport.longitude = oldViewport.longitude
          viewport.latitude = oldViewport.latitude
        }
        else if (maxBounds && lockToBounds) {
          if (viewport.longitude < maxBounds[0]) viewport.longitude = maxBounds[0]
          else if (viewport.longitude > maxBounds[2]) viewport.longitude = maxBounds[2]

          if (viewport.latitude < maxBounds[1]) viewport.latitude = maxBounds[1]
          else if (viewport.latitude > maxBounds[3]) viewport.latitude = maxBounds[3]
        }
        // Here we set the max and min zoom bounds.
        if (lockZoom) {
          viewport.zoom = oldViewport.zoom
        }
        else if (zoombounds) {
          if (viewport.zoom < zoombounds.min) viewport.zoom = zoombounds.min
          else if (viewport.zoom > zoombounds.max) viewport.zoom = zoombounds.max
        }
        // console.log('VIEWPORT', viewport)
        return { viewport }
      }
    }
  ),
  // Unfortunately we need some lifecycle events for when things happen
  lifecycle({
    componentDidMount () {
      const { viewport, onViewportChange, recalcZoomBounds } = this.props
      onViewportChange(viewport)
      recalcZoomBounds(this.props, true)
    },
    componentWillReceiveProps (nextProps) {
      const { recalcZoomBounds, latitude, longitude } = this.props
      // If someone changes the lat/lng props then immediately jump
      if (longitude !== nextProps.longitude || latitude !== nextProps.latitude) {
        nextProps.viewport.latitude = nextProps.latitude
        nextProps.viewport.longitude = nextProps.longitude
      }
      recalcZoomBounds(nextProps)
    }
  })
)(BaseMap)
