import React from 'react'
import PropTypes from 'prop-types'
import { compose, branch, renderComponent } from 'recompose'
import { Auth } from 'aws-amplify'

// Now some graphql stuff
import { withStructuresList } from '../../../graphql/queries'
import Structures from '../misc/Structures'
import Topbar from '../Topbar'

import { Tab, Segment, Button, Header } from 'semantic-ui-react'
import StructureViewer from './StructureViewer'
// import Placeholder from '../../misc/Placeholder'
import { withReduxHome } from '../../../redux/withRedux'
import AddButton from '../misc/AddButton'
import RefreshStockButton from '../misc/RefreshStockButton'
import TheFormComplex from './TheFormComplex'
import sections from '../sections'
import HomeMap from './HomeMap'

import './Homepage.css'

const Homepage = ({ structures }) => (
  <div className='appContainer'>
    <Topbar />
    <Tab className='tabContainer'
      menu={{
        secondary: true,
        inverted: true,
        color: 'green',
        icon: 'labeled',
        size: 'large',
        style: { marginBottom: 0, borderRadius: 0, border: 0 }
      }}
      panes={
        [
          {
            menuItem: {
              key: 'users',
              icon: 'list',
              content: 'List'
            },
            render: () => (
              <Tab.Pane>
                <Structures structures={structures} />
                <AddButton />
              </Tab.Pane>
            )
          },
          {
            menuItem: { key: 'map', icon: 'map', content: 'Map' },
            render: () => (
              <Tab.Pane>
                <HomeMap structures={structures} />
              </Tab.Pane>
            )
          },
          {
            menuItem: { key: 'settings', icon: 'settings', content: 'Settings' },
            render: () => (
              <Tab.Pane>
                <Segment>
                  <Header>Settings</Header>
                  <p>Click this button to add some stock entries.</p>
                  <RefreshStockButton />
                  <p>Or you might want to log out...</p>
                  <Button color='yellow' size='large' icon='lock open' fluid content='Log Out' onClick={() => {
                    Auth.signOut()
                    // This is a hack to force the page to refresh
                    window.location.replace('./')
                  }} />

                </Segment>

              </Tab.Pane>
            )
          }
        ]
      } />
    <div id='Version'>v0.0.25</div>
  </div>
)

Homepage.propTypes = {
  structures: PropTypes.array
}

const TheBDAForm = (props) => <TheFormComplex sections={sections} {...props} />

/**
 * We really should be using React Router or something but for now we only have 3 states anyway:
 *    - List view (the one with tabs)
 *    - Form view (data entry)
 *    - View view (looking at the data being presented)
 */
export default compose(
  withStructuresList,
  withReduxHome,
  branch(({ editing }) => editing && editing.guid && editing.edit, renderComponent(TheBDAForm)),
  branch(({ editing }) => editing && editing.guid && !editing.edit, renderComponent(StructureViewer))
)(Homepage)
